$main: #38241B;
$active: #951205;
$starColor: gray;


.container {
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100vh;

    position: relative;
    ;

    &::-webkit-scrollbar {
        width: 2px;
    }

    &::-webkit-scrollbar-track {
        background-color: white;
        -webkit-border-radius: 2em;
        -moz-border-radius: 2em;
        border-radius: 2em;
    }

    &::-webkit-scrollbar-thumb {
        background-color: $main;
        -webkit-border-radius: 2em;
        -moz-border-radius: 2em;
        border-radius: 2em;
    }

    nav {
        height: 62px;
        background: $main;
        overflow: hidden;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 24px;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 5;

        .imgContainer {
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .navLink {
            display: flex;
            align-items: center;

            button {
                cursor: inherit;
                color: white;
                background: none;
                border: none;

                &:not(:last-child) {
                    margin-right: 24px;
                    font-size: 18px;
                }

                &:last-child {
                    font-size: 20px;
                    padding: 6.5px 25px;
                    background: $active;
                    border-radius: 25px;
                }
            }

        }


    }

    section {
        // scroll-snap-align: start;
    }

    .one {
        padding: 62px 0;
        height: 200vh;
        background: url('./images/bg2.png');
        background-attachment: fixed;
        // background-repeat: no-repeat;
        display: flex;
        justify-content: center;
        position: relative;

        .oneContainer {
            position: sticky;
            top: 15%;
            background: url("./images/bg1.png");
            height: 597px;
            width: 1213px;

            .title {
                position: absolute;
                top: 51px;
                left: 50px;
                display: flex;

                h1 {
                    font-size: 80px;
                    font-weight: 800;
                    color: $main;
                    width: 320px;
                    height: 104px;
                    margin-right: 9px;
                }

                div {
                    color: white;
                    width: 109px;
                    height: 46px;
                    background: $active;
                    border-radius: 14px;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    font-size: 34px;
                    font-family: 'Roboto';
                    font-weight: 700;
                    margin-top: 25px;
                }
            }

            .text1 {
                position: absolute;
                width: 343px;
                height: 180px;
                left: 53px;
                top: 177px;
                font-weight: 900;
                font-size: 42px;
                line-height: 60px;

                color: $main;
            }

            .text2 {
                position: absolute;
                width: 289px;
                height: 140px;
                left: 53px;
                top: 372px;
                font-weight: 400;
                font-size: 24px;
                line-height: 35px;
                color: $main;
            }

            .oneImg {
                position: absolute;
                right: -30px;
                top: -30px;
            }

            .text3 {
                position: absolute;
                right: 61px;
                top: 215px;

                .item {
                    display: flex;
                    align-items: center;

                    div {
                        color: $main;

                        &:first-child {
                            width: 140px;
                            height: 31px;
                            font-weight: 500;
                            font-size: 28px;
                            line-height: 31px;
                            margin-right: 18px;
                        }

                        &:last-child {
                            display: flex;
                            align-items: center;
                            font-size: 28px;

                            span {
                                width: 109px;
                                height: 57px;
                                font-family: 'EB Garamond';
                                font-style: normal;
                                font-weight: 800;
                                font-size: 50px;
                                text-align: end;
                                margin-bottom: 10px;
                            }

                        }
                    }
                }
            }

            .oneImg2 {
                position: absolute;
                width: 672.27px;
                height: 562px;
                left: 308px;
                top: 36px;
            }

            .paperTop {
                position: absolute;
                width: 1280px;
                height: 720px;
                top: -25%;
            }

            .paperDownLeft {
                position: absolute;
                width: 942px;
                height: 1058px;
                bottom: -100%;
                left: -20%;
            }

            .paperDownRight {
                position: absolute;
                width: 1218px;
                height: 1008px;
                bottom: -90%;
                right: -45%;
            }
        }
    }

    .two {
        height: calc(100vh - 62px);
        background: lightblue;
    }




}